import { useChat } from "@ai-sdk/react";
import { useEffect, useMemo, useRef, useState } from "react";
import StarsSvg from "./icons/starsicon";
import SendSvg from "./icons/sendicon";
import ThinkSvg from "./icons/thinkingsvg";
import Markdown from "react-markdown";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { adjustArray } from "./lib";
import { motion } from "framer-motion";
import { v4 as uuidv4 } from "uuid";
import { style } from "./style";
import ExternalLinkSvg from "./icons/externallinksvg";
import CryptoJS from "crypto-js";
import rehypeRaw from "rehype-raw";
import {
  ArrowBigUp,
  ArrowLeft,
  ArrowRight,
  Calendar,
  CalendarDays,
  ExternalLink,
  Loader2,
  MessageCirclePlus,
  Sparkle,
  SquareArrowOutUpRight,
} from "lucide-react";
import axios from "axios";
import PreviewUrl from "./PreviewUrl";
import WhatsAppSvg from "./icons/whatsappsvg";

function App(props) {
  const [isActive, setIsActive] = useState(false);
  const inputRef = useRef(null);
  const scrollRef = useRef(null);
  const [isShowingChat, setIsShowingChat] = useState(props.showchat ?? false);
  const [isCallingTool, setIsCallingTool] = useState(false);
  const [upsellActive, setUpsellActive] = useState(false);
  const [uniqueId, setUniqueId] = useState(uuidv4());

  const [suggestedQuestions, setSuggestedQuestions] = useState(
    props.preloadmessages.split(",")
  );
  const [suggestedQuestionsOpgehaald, setSuggestedQuestionsOpgehaald] =
    useState(false);

  const timestamp = Date.now();

  const secret = encodeSecret(props.botid, timestamp);

  const {
    messages,
    input,
    append,
    error,
    isLoading,
    setMessages,
    handleInputChange,
    handleSubmit,
  } = useChat({
    api: props.chaturl + (props.chaturlend ?? "api/chat/prod"),
    body: {
      botId: props.botid,
      convId: uniqueId,
      timestamp: timestamp,
      token: secret,
    },
    onFinish: async (data) => {
      //  console.log("data", data);
      setStartGettingUrls(true);
      setIsCallingTool(false);
    },
    maxToolRoundtrips: 4,
  });

  if (error) console.error(error.message);

  function encodeSecret(botId, timestamp) {
    const secret = `${botId}:${timestamp}`;
    return CryptoJS.HmacSHA256(secret, botId).toString(CryptoJS.enc.Hex);
  }

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages, upsellActive]);

  // welkom bericht inladen

  useEffect(() => {
    const messagesInLocalStorage = JSON.parse(
      localStorage.getItem("mb-m-his" + "__" + props?.usehistorykey)
    );
    if (
      messages.length === 0 &&
      props.welkombericht &&
      !messagesInLocalStorage &&
      props.welkombericht.length > 2
    ) {
      setMessages([
        ...messages,
        {
          content: props.welkombericht,
          role: "assistant",
          timestamp: new Date().toISOString(),
          id: uuidv4(),
        },
      ]);
    } else {
      if (
        messages.length < 2 &&
        messagesInLocalStorage &&
        props?.usehistorykey.length > 1
      ) {
        console.log("hier 2", messagesInLocalStorage);
        setMessages(messagesInLocalStorage);
        console.log("messages update", messagesInLocalStorage);
      }
    }
  }, [messages, isLoading]);

  useEffect(() => {
    if (messages.length > 0 && props?.usehistorykey) {
      localStorage.setItem(
        "mb-m-his" + "__" + props?.usehistorykey,
        JSON.stringify(messages)
      );
    }
  }, [messages]);

  const [startGettingUrls, setStartGettingUrls] = useState(false);

  const handleSuggestedQuestion = (question, raw) => {
    append({
      content: question,
      role: "user",
      timestamp: new Date().toISOString(),
      id: new Date().toISOString(),
    });
    setSuggestedQuestions((prev) => {
      return prev
        .filter((item) => {
          return item !== question;
        })
        .filter((item) => {
          return item !== raw;
        });
    });
  };

  const CustomMaps = ({ iframe }) => (
    <div>
      <iframe
        title='map'
        src={iframe}
        width='100%'
        height='300'
        allowfullscreen=''
        loading='lazy'
        referrerpolicy='no-referrer-when-downgrade'
      ></iframe>
    </div>
  );

  const CustomLink = ({ href, children }) => {
    return (
      <a
        href={href}
        target='_blank'
        class='bericht-link'
        rel='noopener noreferrer'
      >
        {children} <ExternalLinkSvg fillColor={props.bottextcolor} />
      </a>
    );
  };

  const CustomCalendlyDate = ({ date, url }) => (
    <a className='inline-calendly' target='_blank' rel='noreferrer' href={url}>
      <div className='icon-begin'>
        <CalendarDays size={24} strokeWidth={1.4}></CalendarDays>
      </div>
      <div className='date-en-time'>
        <span className='date-tekst'>
          {new Date(Date.parse(date)).toLocaleDateString()}{" "}
        </span>
        <span className='time-tekst'>
          {new Date(Date.parse(date)).toLocaleTimeString("nl-NL", {
            hour: "2-digit",
            minute: "2-digit",
          })}
        </span>
      </div>
      <SquareArrowOutUpRight
        className='icon-extern'
        size={20}
        strokeWidth={1.5}
      ></SquareArrowOutUpRight>
    </a>
  );

  const useFeaturedLinks = props.usefeaturedlinks;

  const mdComponents = {
    a(props) {
      const { children, node, ...rest } = props;

      if (useFeaturedLinks) {
        console.log("buiten A");
        return <PreviewUrl isLoading={isLoading} {...props} />;
      } else {
        return <CustomLink {...props} />;
      }
    },
    custommaps: ({ node, ...props }) => <CustomMaps {...props} />,
    customcalendly: ({ node, ...props }) => <CustomCalendlyDate {...props} />,
    // code component
    code(props) {
      const { children, className, node, ...rest } = props;
      const match = /language-(\w+)/.exec(className || "");
      return match ? (
        <SyntaxHighlighter
          {...rest}
          PreTag='div'
          children={String(children).replace(/\n$/, "")}
          language={match[1]}
        />
      ) : (
        <code {...rest} className={className}>
          {children}
        </code>
      );
    },
  };

  return (
    <>
      <div className={`trigger-knop ${isActive ? "is-active-open" : ""} `}>
        <div className='knop-glow-outer'>
          <div className='knop-glow-inner'></div>
        </div>
        <div className='glow-line-outer'>
          <div className='glow-line-inner'></div>
          <button
            onClick={() => {
              setIsActive(!isActive);
            }}
          >
            <Sparkle className='side-icon' size={18}></Sparkle>
            {isActive ? "Sluiten" : "AI Assistent en zoeken"}
          </button>
        </div>
      </div>

      <div dangerouslySetInnerHTML={{ __html: style(props) }}></div>

      <div className={`tool-container ${isActive && "tool-is-actief"} `}>
        <div
          onClick={() => {
            setIsActive(false);
          }}
          className='tool-background'
        ></div>

        <div className='berichten-positie'>
          <div
            onClick={() => {
              setIsActive(false);
            }}
            className='sluit-trigger'
          ></div>

          <div className={`aifaq-container`}>
            <label className='ai-label'>
              <Sparkle className='side-icon' size={18}></Sparkle>
              AI Assistent en zoeken
            </label>
            <div className={`berichten-container  `}>
              <div className='berichten-container-inner' ref={scrollRef}>
                {messages.map((m) => {
                  if (m.toolInvocations) return null;
                  return (
                    <div
                      key={m.id}
                      className={`bericht ${
                        m.role === "user" ? "user-bericht " : "ai-bericht "
                      }`}
                    >
                      {!m.html && (
                        <Markdown
                          children={m?.content}
                          rehypePlugins={[rehypeRaw]} // Nodig om HTML te parsen
                          components={mdComponents}
                        />
                      )}
                      {m.html && (
                        <p dangerouslySetInnerHTML={{ __html: m?.html }}></p>
                      )}
                    </div>
                  );
                })}

                {isLoading &&
                  messages[messages.length - 1]?.role !== "assistant" &&
                  !messages[messages.length - 1]?.toolInvocations && (
                    <div class='bericht ai-bericht thinking-bericht'>
                      <ThinkSvg fillColor={props.bottextcolor} />
                    </div>
                  )}

                {messages[messages.length - 1]?.toolInvocations &&
                  messages[messages.length - 1]?.toolInvocations.length > 0 &&
                  messages[messages.length - 1]?.toolInvocations[0]
                    ?.toolName === "getCalendly" && (
                    <div class='bericht ai-bericht tool-thinking-bericht '>
                      <ThinkSvg fillColor={props.bottextcolor} />
                      AI zoekt data in agenda{" "}
                      {/* <Loader2 className='loader-icoon' size={16}></Loader2> */}
                    </div>
                  )}

                {messages[messages.length - 1]?.toolInvocations &&
                  messages[messages.length - 1]?.toolInvocations.length > 0 &&
                  messages[messages.length - 1]?.toolInvocations[0]
                    ?.toolName === "websiteScan" && (
                    <div class='bericht ai-bericht tool-thinking-bericht '>
                      <ThinkSvg fillColor={props.bottextcolor} />
                      AI bekijkt je website{" "}
                      {/* <Loader2 className='loader-icoon' size={16}></Loader2> */}
                    </div>
                  )}

                {error && !error.message.includes("network") && (
                  <div class='bericht ai-bericht error-bericht '>
                    <p>{error.message}</p>
                  </div>
                )}
              </div>
              <div className='form-container'>
                <form
                  className={`verstuur-form ${
                    isShowingChat ||
                    (isShowingChat === false && messages.length > 0)
                      ? "toon-wel"
                      : "toon-niet"
                  }`}
                  onSubmit={handleSubmit}
                >
                  <input
                    ref={inputRef}
                    className='verstuur-input'
                    value={input}
                    placeholder='Stel je vraag of zoekopdracht aan AI'
                    onChange={handleInputChange}
                  />
                  <button
                    // style={{ backgroundColor: props.accentcolor }}
                    className='verstuur-button'
                    type={isLoading ? "buton" : "submit"}
                    disabled={isLoading || input.length === 0}
                  >
                    <div className='verstuur-button-icon'>
                      <SendSvg fillColor={props.iconcolor} />
                    </div>

                    <div className='glitter-layer'></div>
                  </button>
                </form>
              </div>
            </div>
            <div className='onder-ai'>
              <div className='onder-ai-links'>
                {suggestedQuestions?.length > 0 && (
                  <>
                    <label className='ai-label'>Suggesties</label>
                    <div
                      className={`suggested-question-container ${
                        isLoading ? "pointer-events-none" : ""
                      }`}
                    >
                      {suggestedQuestions.map((question, index) => {
                        // split context eruit
                        let message = question;
                        let onlyQuestion = null;

                        if (question.includes(":::")) {
                          const splitMessage = question.split(":::");
                          onlyQuestion = splitMessage[1];
                          message = `<span class="msg-context"> Context: ${splitMessage[0]}. Bericht:</span> ${splitMessage[1]}`;
                          // message = `Context: ${splitMessage[0]}. Bericht: ${splitMessage[1]}`;
                        }

                        return (
                          <button
                            disabled={isLoading}
                            onClick={() =>
                              handleSuggestedQuestion(
                                message,
                                onlyQuestion && question
                              )
                            }
                            key={`${question}-${index}`}
                            className='suggested-question'
                          >
                            <div>
                              <StarsSvg fillColor={"#fff"} />
                              <span className='suggested-question-tekst'>
                                {" "}
                                {onlyQuestion || question}
                              </span>
                            </div>
                            <ArrowRight
                              size={16}
                              className='sugg-pijl'
                            ></ArrowRight>
                          </button>
                        );
                      })}
                      {!isShowingChat && messages.length === 0 && (
                        <div
                          className='open-chat-plus'
                          onClick={() => {
                            setIsShowingChat(true);
                          }}
                        >
                          <MessageCirclePlus size={18}></MessageCirclePlus>
                        </div>
                      )}
                    </div>
                  </>
                )}
              </div>

              <div className='onder-ai-rechts'>
                <label className='ai-label'>Snel naar</label>
                <a
                  href='https://calendly.com/slimmerewebsite/ai-demo'
                  target='_blank'
                  without
                  rel='noreferrer'
                  className='snel-naar-knop'
                >
                  <Calendar size={20}></Calendar>
                  Meeting plannen
                </a>
                <a
                  href='https://wa.me/31653285754'
                  target='_blank'
                  without
                  rel='noreferrer'
                  className='snel-naar-knop'
                >
                  <WhatsAppSvg />
                  WhatsApp bericht sturen
                </a>
                {/* <a
                  href='https://calendly.com/slimmerewebsite/ai-demo'
                  target='_blank'
                  without
                  rel='noreferrer'
                  className='snel-naar-knop'
                >
                  Support docs
                </a> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
