import React, { useEffect, useState, useMemo } from "react";
import { Loader2 } from "lucide-react";
import axios from "axios";

const getLinkPreview = async (href) => {
  console.log("ik ga url halen");

  try {
    const res = await axios.post(
      "https://login.slimmerewebsite.nl/api/linkpreview",
      {
        url: href,
      }
    );

    if (res.data) {
      console.log(res.data);
      sessionStorage.setItem(href, JSON.stringify(res.data));
      return res.data;
    } else {
      return undefined;
    }
  } catch {
    return undefined;
  }
};

function PreviewUrl({ href, isLoading }) {
  const dataAsStrInitial = sessionStorage.getItem(href);
  const dataAsObjInitial = dataAsStrInitial && JSON.parse(dataAsStrInitial);

  const [previewData, setPreviewData] = useState(dataAsObjInitial ?? undefined);

  const storage = sessionStorage.getItem(href);

  useEffect(() => {
    console.log("storage voor", storage);

    if (!storage && !isLoading) {
      console.log("href", href);
      getLinkPreview(href).then(() => {
        const dataAsStr = sessionStorage.getItem(href);
        const dataAsObj = JSON.parse(dataAsStr);

        setPreviewData(dataAsObj);
      });
    } else {
      console.log("storage na", storage);
      const dataAsStr = sessionStorage.getItem(href);
      const dataAsObj = JSON.parse(dataAsStr);
      setPreviewData(dataAsObj);
    }
  }, [href, isLoading, storage]);

  const imageSrc = useMemo(() => previewData?.image, [previewData?.image]);

  return (
    <a
      target='_blank'
      className='preview-url-container'
      href={href}
      rel='noreferrer'
    >
      {!previewData && (
        <div className='url-voor-preview-data'>
          <p className='url-tekst'>{href}</p>
          <p className='loading-tekst'>
            We halen url preview op{" "}
            <Loader2 size={20} className='loader-preview'></Loader2>
          </p>
        </div>
      )}
      {previewData && previewData?.title && previewData?.description && (
        <div className='url-met-preview-data'>
          {imageSrc && <img className='url-img' src={imageSrc} alt='preview' />}
          <div className='meta-tekst'>
            <p className='url-title'>{previewData?.title}</p>
            <p className='url-meta'>{previewData?.description}</p>
          </div>
        </div>
      )}
      {!previewData?.description && (
        <span className='inline-url-geen-preview'>{href}</span>
      )}
    </a>
  );
}

export default PreviewUrl;
